  }
}

/* --------------------------------

JavaScript Disabled

-------------------------------- */
.no-js .cd-main-header {
  height: auto;
}
.no-js .cd-main-header::after {
  clear: both;
  content: "";
  display: table;
}

.no-js .cd-nav-trigger {
  display: none;

}

.no-js .cd-main-nav-wrapper {
  display: inline-block;
  float: right;
}

.no-js .cd-main-nav {
  position: static;
  width: auto;
  height: auto;
  visibility: visible;
  background-color: transparent;
  float: right;
}
.no-js .cd-main-nav::after {
  clear: both;
  content: "";
  display: table;
}
.no-js .cd-main-nav li {
  display: inline-block;
  float: left;
}
.no-js .cd-main-nav a {
  border-bottom: 0;
}

.no-js .cd-search-trigger {
  display: inline-block;
  text-indent: 0;
  color: $main-blue;
  float: right;
  line-height: 50px;
}
@media only screen and (min-width: 1024px) {
  .no-js .cd-search-trigger {
    text-indent: 100%;
    color: transparent;
    line-height: normal;
  }
}

.no-js .cd-main-search {
  position: static;
  opacity: 1;
  visibility: visible;
  padding: 2em 5%;
  background: #333545;
}
.no-js .cd-main-search form {
  margin: 0;
}
.no-js .cd-main-search input[type="search"] {
  background: #333545;
}
.no-js .cd-main-search select {
  position: relative;
  color: $main-blue;
  opacity: 1;
  -webkit-transform: translateY(0);
  -moz-transform: translateY(0);
  -ms-transform: translateY(0);
  -o-transform: translateY(0);
  transform: translateY(0);
}
.no-js .cd-main-search select:focus {
  background-color: #3d3f4e;
}
.no-js .cd-main-search .selected-value {
  display: none;
}


/* DropDown */
@media (max-width: 1024px) {

.cd-main-nav-wrapper {
  .dropdown-menu {
    border: 0;
    padding: 0;
    background: none;
    position: inherit!important;
    width: 100%;
    margin: 0;
    transform: none!important;
  }
  .dropdown-divider {
    display: none;
  }
}

}

/* Custom */
.quick-links,
.news {
  ul {
    margin: 0;
    padding: 0;
    list-style: none
  }
}


/* Flags */
.flags-dropdown {
  .dropdown-menu{
    min-width: 3rem;
    @media (max-width: 1024px) {
      min-width: inherit;

    }
    &.show {
    left: 5px!important;
      @media (max-width: 1024px) {
        left: 0!important;
      }
    }
  }
}


/* new Menu elements */
.cd-main-nav {
  .no-effect {
    a {
      &:hover{
        opacity: 1;
      }
    }
  }

  .btn-menu {

    a {
      background: none;

      @include mq(lg2) {
        background: $yellow;
        border-radius: 25px;
        color: $main-blue;
        font-size: 14px;
        line-height: 1;
        @include rem(padding, 10px 15px);

        &:hover{
          background: $darkblue;
          color: $white;
        }
      }
    }
  }
}
.cd-main-nav {
    .no-effect a {
      pointer-events: none;
  }
}


// badge icon
.badge-top {
  margin: 4px 0 0 5px ;



  .fa-stack{
    width: 30px;
  }
  .fa-stack[data-count]:after{
    position:absolute;
    right:50%;
    top:10px;
    z-index: 1;
    content: attr(data-count);
    font-size: 10px;
    padding:.5em .6em;
    border-radius:999px;
    line-height:.75em;
    color: white;
    background:rgba(255,0,0,.85);
    text-align:center;
    font-weight: bold;
  }
}

